import React from 'react';
import './Form.css';
import { useRef } from 'react';
import emailjs from 'emailjs-com';

const Form = () => {
    const form = useRef();

    const sendMail = (e) => {
        e.preventDefault();
        emailjs.sendForm('service_pnvz8z6','template_rf3879x',form.current,'user_ubipvwIhsyqQzy3VcDLcv')
        .then((result) => {
            console.log(result.text);
        },(error) => {
            console.log(error.text)
        })
        e.target.reset()
    }

    return(
        <>
                    {/* FORM */}
                <legend>let's rock!</legend>
                <form ref={form} onSubmit={sendMail}>
                    <label className="username" id="username">name</label>
                        <input type="text" className="userinput" name="name"></input>
                    <label className="email" id="email">email</label>
                        <input type="email" className="emailinput" name="email"></input>
                    <label className="subject" id="subject">brief about your project</label>
                        <input type="message" className="subjectinput" name="message"></input>
                    <button type="submit" className="form-button">Send Mail</button>
                </form>
        </>
    )
}
export default Form;