import React, { useState,useEffect } from 'react';
import './Home.css'
import Hero from './Hero/Hero';
import ProjectPage from './projectPage/projectPage';

const Home = () => {
    const [ cursorX, setCursorX ] = useState();
    const [ cursorY, setCursorY ] = useState();

    const mousemove = (e) => {
        setCursorX(e.clientX);
        setCursorY(e.clientY);
    }

    useEffect(() => {
        window.addEventListener('mousemove', mousemove)

        return () => {
            window.removeEventListener('mousemove', mousemove)
        }
        
    })
    const [ slideX , setSlideX ] = useState(810)
    const [ offSetY, setOffSetY ] = useState(0)
    const [ projectOffSetY, setProjectOffSetY ] = useState(0)

    const wrapper = document.getElementById('wrapper')
    const handleScroll = () => {

        const project = document.getElementById('projects')
        const hero = document.getElementById('hero')
        const divider = document.getElementById('divider')
        if( hero.getBoundingClientRect().top > 0){
            setOffSetY(hero.getBoundingClientRect().top)
        }else{
            setOffSetY(-hero.getBoundingClientRect().top)
        }

        console.log(offSetY)
        setSlideX(divider.getBoundingClientRect().bottom - window.innerHeight)
        setProjectOffSetY(project.getBoundingClientRect().top)
    }
    return (
        <>
        <div onScroll={handleScroll} className="home-container">
            <div className="home-wrapper" id="wrapper">
            <div id="hero" className="home-hero-container" style={{ transform: `translateY(${offSetY * 0.5}px)`}}>
                <div className="home-hero-wrapper">
                    <Hero />
                </div>
            </div>

            <div style={{ height: 'fit-content'}} className="divider-container" id="divider">
            <div className="divider-wrapper">
            <div className="divider-text-wrapper">
                <div className="divider-text1"
                style={{transform: `translateX(${slideX * 0.2}px) `}}
                >PePu<span className="divider-span">PePu</span></div>
                <div className="divider-text2"
                style={{transform: `translateX(${slideX * -0.2}px) `}}
                ><span className="divider-span">PePu</span>PePu</div>
                
            </div>
            </div>
        </div>
        

        <div id="projects" className="home-projectPage-container">
            <div className="home-projectPage-wrapper">
                <ProjectPage height={projectOffSetY} />
            </div>
        </div>
            <div className="mouse-demo"
            style={{
                left: cursorX + 'px',
                top: cursorY + 'px'
            }}></div>
        </div>
        </div>
        </>
    )
}
export default Home;